
.leftGutter {
  display: grid;
  grid-row: body;
  grid-column: leftGutter;
}
.rightGutter {
  display: grid;
  grid-row: body;
  grid-column: rightGutter;
}

.formWrapper {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}

.input {
  display: flex;
  flex: 50% 1;
  width: 50%;
  margin: 3px;
  background-color: #efe9cc;
  font-family: "spectral";
}
.submitButton {
  width: 25%;
  padding: 4px;
  border-radius: 4px;
  color: #17252a;
  border: 1px solid #262636;
  background-color: #efe9cc;
  cursor: pointer;
  font-weight: 500;
  -webkit-transition: 0.15s ease-in-out;
  transition: 0.15s ease-in-out;
  font-family: "Spectral", sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 1.1;
  margin: 3px;
  text-transform: uppercase;
  cursor: pointer;
}

.submitButton:hover {
  color: black;
  background-color: #ccefd8;
}

.searchedBookWrapper {
  height: 100vh;
  overflow: auto;
  display: grid;
  grid-column: searchedBooks;
  grid-row: body;
  border-right: 1px solid #3c3a333a;
}
.searchedBookWrapper h2 {
  text-align: center;
}

.bookItem {
  display: flex;
  flex-direction: column;
  margin: 15px;
}
.bookImage {
  max-height: 200px;
  width: auto;
  border: 1px solid #3c3a33;
}

.title {
  font-family: "Cormorant Garamond", serif;
  font-size: 22px;
  color: #1e1d1a;
}

.author {
  font-family: "Spectral", serif;
  font-style: italic;
  font-weight: 200;
  font-size: 16px;
  color: #3c3a33;
}

.description {
  font-family: "Spectral", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.1em;
  color: #1e1d1a;
}
.saveBook {
  width: 25%;
  padding: 4px;
  border-radius: 4px;
  color: #17252a;
  border: 1px solid #262636;
  background-color: #efe9cc;
  cursor: pointer;
  font-weight: 500;
  -webkit-transition: 0.15s ease-in-out;
  transition: 0.15s ease-in-out;
  font-family: "Spectral", sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 1.1;
  margin: 5px 10px 5px 10px;
  text-transform: uppercase;
  cursor: pointer;
}

.saveBook:hover {
  color: black;
  background-color: #ccefd8;
}

.saveBook.savedActive {
  color: black;
  background-color: #d2efcc;
  pointer-events: none;
}

.savedBookWrapper {
  height: 100vh;
  overflow: auto;
  display: grid;
  grid-column: savedBooks;
  grid-row: body;
  border-left: 1px solid #3c3a333a;
}
.savedBookWrapper h2 {
  text-align: center;
}

.bookItem {
  display: flex;
  flex-direction: column;
  margin: 15px;
}
.bookImage {
  max-height: 200px;
  width: auto;
  border: 1px solid #3c3a33;
}

.title {
  font-family: "Cormorant Garamond", serif;
  font-size: 22px;
  color: #1e1d1a;
}

.author {
  font-family: "Spectral", serif;
  font-style: italic;
  font-weight: 200;
  font-size: 16px;
  color: #3c3a33;
}

.description {
  font-family: "Spectral", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.1em;
  color: #1e1d1a;
}
.buttonDelete {
  width: 25%;
}

.buttonDelete {
  width: 25%;
  padding: 4px;
  border-radius: 4px;
  color: #17252a;
  border: 1px solid #262636;
  background-color: #efe9cc;
  cursor: pointer;
  font-weight: 500;
  -webkit-transition: 0.15s ease-in-out;
  transition: 0.15s ease-in-out;
  font-family: "Spectral", sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 1.1;
  margin: 5px 10px 5px 10px;
  text-transform: uppercase;
  cursor: pointer;
}

.buttonDelete:hover {
  color: black;
  background-color: #efcce4;
}

.wrapper {
  min-height: 100vh;
  background-color: #eadea6;
  display: grid;
  grid-template-columns: [leftGutter] 1fr [searchedBooks] 5fr [savedBooks] 5fr [rightGutter] 1fr;
  grid-template-rows: [header] 150px [subHeader] 100px [body] 1fr [footer] 100px;
}

.headerWrapper {
  display: grid;
  grid-column: span 12;
  grid-row: header;
  background: #efe9cc;
}
.header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header > h1 {
  font-family: "Cormorant Garamond", serif;
  font-size: 32px;
  color: #1e1d1a;
}
.header-border {
  border-bottom: 1px solid #3c3a333a;
  width: 75%;
  margin: auto;
}

.subWrapper {
  display: grid;
  grid-row: subHeader;
  grid-column: span 10;
  border-bottom: 2px solid #3c3a333a;
  background-color: #efe9cc;
}
.sub {
  display: flex;
}
.sub-header-left {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 50% 1;
  height: 100%;
  margin: auto;
}
.sub-header-right {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 50% 1;
  height: 100%;
  margin: auto;
}
.sub > h2 {
  font-family: "Cormorant Garamond", serif;
  font-size: 27px;
}

.footerWrapper {
  display: grid;
  grid-row: footer;
  grid-column: span 12;
  background: #deb881;
}
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
}

.footerText {
  font-family: "Spectral", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.1em;
  color: #1e1d1a;
}

.footerText > a {
  color: #efe9cc;
  -webkit-transition: all ease-in-out 0.2s;
  transition: all ease-in-out 0.2s;
}

.footerText > a:hover {
  color: #ccefd8;
}

span.icons {
  display: flex;
  justify-content: center;
  flex-direction: row;
}

span.icons > a {
  color: #efe9cc;
  -webkit-transition: all ease-in-out 0.2s;
  transition: all ease-in-out 0.2s;
}
span.icons > a:hover {
  color: #ccefd8;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

